import React, { useState } from "react";
import SearchBar from "./SearchBar";
import SendIcon from "@mui/icons-material/Send";
import Button from "@mui/material/Button";
import Stack from "@mui/material/Stack";

const searchBarBlueprint = {
  id: makeid(),
  name: "",
  localization: "",
  from: "",
  to: "",
  price: 0,
};

function makeid() {
  var length = 4;
  var result = "";
  var characters =
    "ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789";
  var charactersLength = characters.length;
  for (var i = 0; i < length; i++) {
    result += characters.charAt(Math.floor(Math.random() * charactersLength));
  }
  return result;
}

export default function SearchBarList() {
  const [searchBarList, setSearchBarList] = useState([
    Object.assign({}, searchBarBlueprint),
  ]);
  const addSearchBar = () => {
    setSearchBarList(
      searchBarList.concat({ ...searchBarBlueprint, ...{ id: makeid() } })
    );
  };
  const removeSearchBar = (indexToBeRemoved) => {
    setSearchBarList(
      searchBarList.filter((item, index) => {
        return item.id !== indexToBeRemoved;
      })
    );
  };
  const updateData = (data) => {
    setSearchBarList(
      searchBarList.map((object) => (object.id === data.id ? data : object))
    );
  };
  return (
    <>
      {searchBarList.map((searchBar, index) => (
        <SearchBar
          key={index}
          updateData={updateData}
          data={searchBar}
          addSearchBar={addSearchBar}
          isMultipleRow={searchBarList.length > 1}
          removeSearchBar={removeSearchBar}
        />
      ))}
      {searchBarList.length > 1 ? (
        <Stack>
            <Button variant="contained" endIcon={<SendIcon />}>
              Zamawiam
            </Button>
        </Stack>
      ) : (
        <></>
      )}
    </>
  );
}
